    position: relative;
}
.media_name:before {
    position: absolute;
    display: block;
    width: 200%;
    height: 200%;
    content: "";
    border: 1px solid #ABB2B9;
    top: 0;
    transform-origin: -2% 0;
    transform: scale(0.5, 0.5)
}
.media_img {
    width: 45px;
}
.media_text {
    display: inline-block;
    line-height: 25px
}
.content {
