    margin: .2rem auto;
    width: 70%
}
.key_words {
    margin: .5rem auto;
    border-bottom: 1px solid #ccc
}
.key_tag {
    border: 1px solid #ccc;
    color: #657180 !important;
    margin: 5px !important;
}
.zan {
    display: -ms-flexbox;
